.Footer-container {
    position: relative;
}
.Footer-container > hr {
    border: 1px solid var(--lightgray);
}
.footer {
    padding: 1rem 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 20rem;
    gap: 4rem;
}
.social-links {
    display: flex;
    gap: 4rem;
}
.social-links > img {
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}   
.logo-f > img {
    width: 10rem;
}
.blur-f-1 {
    width: 26rem;
    height: 12rem;
    bottom: 0;
    right: 15%;
    /* filter: blur(200px); */
    background: rgb(255, 115, 0);
}
