.header {
    display: flex;
    justify-content: space-between;
}
.header-logo {
    width: 10rem;
    height: 3rem;
}
.header-menu {
    list-style: none;
    display: flex;
    gap: 2rem;
    color: white;
}
.header-menu > li:hover {
    color: var(--orange);
    cursor: pointer;
}