.plans-container {
    display: flex;
    flex-direction: column;
    margin-top: -4rem;
    padding: 0 2rem;
    gap: 4rem;
    position: relative;
}
.plans {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.plan {
    display: flex;
    flex-direction: column;
    background-color: var(--caloryCard);
    color: white;
    gap: 2rem;
    padding: 1.5rem;
    width: 15rem;
}
.plans-blur-1 {
    width: 32rem;
    height: 23rem;
    top: 6rem;
    left: 0rem;
}
.plans-blur-2 {
    width: 32rem;
    height: 23rem;
    top: 10rem;
    right: 0rem;
}
.plan:nth-child(2) {
    /* background-color: var(--planCard); */
    background: linear-gradient(210.41deg, #fa5042 1.14%, #ffa739 100.75%);
    transform: scale(1.1);
}
.plan > svg {
    width: 2rem;
    height: 2rem;
    fill: var(--orange);
}
.plan > :nth-child(2) {
    font-size: 1rem;
    font-weight: bold;
}
.plan > :nth-child(3) {
    font-size: 3rem;
    font-weight: bold;
}
.plan > :nth-child(5) {
    font-size: 0.8rem;
}
.features {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.feature {
    display: flex;
    gap: 1rem;
    align-items: center;
}
.feature > img {
    width: 1rem;
}
.plans >:nth-child(2) > svg {
    fill: white;
}
.plans >:nth-child(2) > button {
    color: orange;
}